import React from 'react'
import { useSelector } from 'react-redux'
import LineBlock from '../../../lib/components/LineBlock'

// import PropTypes from 'prop-types'

import './DesBlock.styles.scss'

const DesBlock = () => {
  const dataDesBlock = useSelector((state) => state.desAdv.OrderData)
  // console.log(dataDesBlock)
  return (
    <div className="des-block">
      {dataDesBlock &&
        dataDesBlock.length &&
        dataDesBlock.map((data) => <LineBlock data={data} />)}
    </div>
  )
}

